import React, { useState, useEffect } from 'react';
import { StyleSheet, Text, View, Image, Button, TouchableOpacity, TouchableWithoutFeedback, FlatList, StatusBar, Switch, Linking } from 'react-native';
import data from './assets/cards.json';
import SmallClassImage from './SmallClassImage';
import SoundButton from './SoundButton';
import SoundDivider from './SoundDivider';
import Card from './Card';
import FlatListItemSeparator from './FlatListItemSeparator';
import cardsets from './assets/cardsets.json';
import heroes from './assets/heroes.json';
import tokens from './assets/tokens.json';
import { NavigationContainer, useScrollToTop, useIsFocused } from '@react-navigation/native';
import { Toolbar } from 'react-native-material-ui';
import { MaterialCommunityIcons } from '@expo/vector-icons';
import { useFonts, Roboto_900Black, Roboto_500Medium, Roboto_700Bold} from '@expo-google-fonts/roboto';

global.tokenIDs = tokens.map(a => a.tokenID)

const CardListTab = ( { route, navigation, state }) => {

    let [fontsLoaded] = useFonts({
      Roboto_700Bold,
    });

  React.useLayoutEffect( () => {
    navigation.setOptions({headerShown: false});
  }, [navigation]);

  const flatListRef = React.useRef()
  useScrollToTop(flatListRef)

  var {cardSet} = route.params
  const {typeFilter} = route.params

  var myData = filterData()
  const heroIDs = heroes.map(a => a.id)

  function filterData() {
    const heroIDs = heroes.map(a => a.id)
    var filteredData;

    if (typeFilter == 'MINION') {
      filteredData = data
        .filter(x => (!heroIDs.includes(x.id)))
    }
    else {
      filteredData = data
        .filter(x => heroIDs.includes(x.id))
    }

    return filteredData
      .sort(

          function(a,b) {
          var classA = classOrder(a)
          var classB = classOrder(b)

          if(classA < classB  ) { return -1}
          if(classA > classB ) { return 1}
          if(classA == classB ) {
            if(a.name < b.name ) { return -1}
            if(a.name  > b.name ) { return 1}
            if(a.name  == b.name ) {return 0 }
          }
          }
        )

        function classOrder(a) {

          // Dual Class Cards
          switch(a.multiClassGroup) {
            case 'HUNTER_DEMONHUNTER': return 1.5
            case 'DRUID_SHAMAN': return 2.5
            case 'DRUID_HUNTER': return 3.5
            case 'MAGE_ROGUE': return 4.5
            case 'PALADIN_PRIEST': return 5.5
            case 'PRIEST_WARLOCK': return 6.5
            case 'ROGUE_WARRIOR': return 7.5
            case 'MAGE_SHAMAN': return 8.5
            case 'WARLOCK_DEMONHUNTER': return 9.5
            case 'PALADIN_WARRIOR': return 10.5
          }

          switch(a.cardClass) {
            case 'DEMONHUNTER': return 1
            case 'DRUID': return 2
            case 'HUNTER': return 3
            case 'MAGE': return 4
            case 'PALADIN': return 5
            case 'PRIEST': return 6
            case 'ROGUE': return 7
            case 'SHAMAN': return 8
            case 'WARLOCK': return 9
            case 'WARRIOR': return 10
            default: return 99
          }
        }

    }

    const [includeMinions, setIncludeMinions] = useState(true)
    const [includeHeroes, setIncludeHeroes] = useState(true)
    const [includeSpells, setIncludeSpells] = useState(false)
    const [includeWeapons, setIncludeWeapons] = useState(false)
    const [includeNonCollectible, setIncludeNonCollectible] = useState(false)
    const [cardDisplayed, setCardDisplayed] = useState('none')
    const [card, setCard] = useState(data[0])
    const [myCardData, setMyCardData] = useState([])
    const [cards, setCards] = useState([])
    const [loadLimit, setLoadLimit] = useState(100)
    const [showFooter, setShowFooter] = useState(true)
    const [filterMenuVisibility, setFilterMenuVisibility] = useState('hidden')
    const [infoModalVisibility, setInfoModalVisibility] = useState('hidden')

  function search(criteria) {
    if(criteria != "") {
      setMyCardData(myData.filter(x =>
        (
          x.type == (includeMinions ? 'MINION' : '') ||
          x.type == (includeHeroes ? 'HERO' : '') ||
          x.type == (includeSpells ? 'SPELL' : '') ||
          x.type == (includeWeapons ? 'WEAPON' : '')
        ) && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
          && (x.set === cardSet || cardSet === null)
          && (x.name.toUpperCase()).includes(criteria.toUpperCase())))
      setShowFooter(false)
    }
    else {
      setMyCardData(myData.filter(x =>
        (
          x.type == (includeMinions ? 'MINION' : '') ||
          x.type == (includeHeroes ? 'HERO' : '') ||
          x.type == (includeSpells ? 'SPELL' : '') ||
          x.type == (includeWeapons ? 'WEAPON' : '')
        )
          && (x.set === cardSet || cardSet === null)
          && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
        ).slice(0,loadLimit))
      setShowFooter(true)
    }
  }

  function closeSearch() {
    setMyCardData(myData.filter(x =>
      (
        x.type == (includeMinions ? 'MINION' : '') ||
        x.type == (includeHeroes ? 'HERO' : '') ||
        x.type == (includeSpells ? 'SPELL' : '') ||
        x.type == (includeWeapons ? 'WEAPON' : '')
      )
        && (x.set === cardSet || cardSet === null)
        && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
      ).slice(0,loadLimit))
    setShowFooter(true)
  }

  navigation.addListener('tabPress', e => {
    if (typeFilter == 'MINION') {
      cardSet = null
      navigation.navigate(
        'Cards',
        {cardSet: null})
        myData = filterData()
        setMyCardData(myData.filter(x =>
          (
            x.type == (includeMinions ? 'MINION' : '') ||
            x.type == (includeHeroes ? 'HERO' : '') ||
            x.type == (includeSpells ? 'SPELL' : '') ||
            x.type == (includeWeapons ? 'WEAPON' : '')
          )
            && (x.set === cardSet || cardSet === null)
            && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
          ).slice(0,loadLimit))
    }
    else {
      setMyCardData(myData)
      navigation.navigate(
        'Heroes',
        {cardSet: null})
    }
  })

  navigation.addListener('focus', e => {
    goToTop()
    if (typeFilter == 'MINION' && cardSet != null) {
      setMyCardData(myData.filter(x =>
        (
          x.type == (includeMinions ? 'MINION' : '') ||
          x.type == (includeHeroes ? 'HERO' : '') ||
          x.type == (includeSpells ? 'SPELL' : '') ||
          x.type == (includeWeapons ? 'WEAPON' : '')
        )
          && (x.set == cardSet)
          && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
        ))
    }
    else if (typeFilter == 'MINION' && cardSet == null) {
      setMyCardData(myData.filter(x =>
        (
          x.type == (includeMinions ? 'MINION' : '') ||
          x.type == (includeHeroes ? 'HERO' : '') ||
          x.type == (includeSpells ? 'SPELL' : '') ||
          x.type == (includeWeapons ? 'WEAPON' : '')
        )
          && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
        ).slice(0,loadLimit))
    }
    else {
      setMyCardData(myData)
    }
  })

  function goToTop() {
    flatListRef.current.scrollToOffset({ animated: true, offset: 0 })
  }

  const loadMore = () => {
    setMyCardData(myCardData.concat(myData.filter(x =>
        (
            x.type == (includeMinions ? 'MINION' : '') ||
            x.type == (includeHeroes ? 'HERO' : '') ||
            x.type == (includeSpells ? 'SPELL' : '') ||
            x.type == (includeWeapons ? 'WEAPON' : '')
          )
            && (x.set === cardSet || cardSet === null)
            && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
          ).slice(loadLimit,loadLimit+100)))
    setLoadLimit(loadLimit+100)
  }

  const openNewCard = (newCardID) => {
    var newCard = data.find(x => x.id == newCardID)
    cards.push(newCard)
    setCard(newCard)
  }

  const closeCards = () => {
    setCardDisplayed('none')
  }

  const returnToPreviousCard = () => {
    setCard(cards[cards.length-2])
    cards.pop()
  }

  const CardListFooter = () => {
    return (
      <>
      { (myCardData.length < myData.filter(x =>
        (
          x.type == (includeMinions ? 'MINION' : '') ||
          x.type == (includeHeroes ? 'HERO' : '') ||
          x.type == (includeSpells ? 'SPELL' : '') ||
          x.type == (includeWeapons ? 'WEAPON' : '')
        )
        && (x.set === cardSet || cardSet === null)
        && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))).length && showFooter) ?
        <TouchableOpacity style={styles.button} onPress= {() => loadMore()}>
          <Text style={styles.text}>Load More</Text>
        </TouchableOpacity>
        : <></>
      }
      </>
    )
  }

  const copyToClipboard = () => {
    Clipboard.setString('hello world')
  }

  const InfoModal = () => {
    return (
      <View style={{position: 'absolute', top: '30%', left: 0, right: 0, marginLeft: 'auto', marginRight: 'auto', zIndex: 1, backgroundColor: 'white', visibility:infoModalVisibility, padding: 16, textAlign: 'center', width: '25%',
        shadowColor: "#000",
        shadowOffset: {
          width: 0,
          height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 3.84,
        elevation: 5}} >
        <Text style={{padding: 8}}>Thank you for using HearthSFX!</Text>
        <Text style={{padding: 8}}>{'For inquiries, suggestions, or ideas please reach out to me at'}
          <TouchableWithoutFeedback onPress={() => {Linking.openURL('mailto:hearthsfx@gmail.com')}}>
            <View>
              <Text style={{color:'blue', textDecorationLine: 'underline'}}>hearthsfx@gmail.com</Text>
            </View>
          </TouchableWithoutFeedback>
        </Text>
        <SoundDivider/>
        <View style={{paddingBottom: 8}}>
          <Text style={{paddingBottom: 8}}>Interested in helping cover some of the hosting costs? Support me using any of the following:</Text>
          <View style={{flexDirection: 'row', justifyContent: 'space', alignItems: 'center', paddingBottom: 8}}>
              <TouchableWithoutFeedback onPress={() => {Linking.openURL('https://ko-fi.com/hearthsfx')}}>
                <Image style={{width: 34, height: 34}} source={require('./assets/img/kofi.png')}/>
              </TouchableWithoutFeedback>
            <Text style={{paddingLeft: 8}}>Ko-fi</Text>
          </View>
          <View style={{flexDirection: 'row', justifyContent: 'space', alignItems: 'center', paddingBottom: 8}}>
            <TouchableWithoutFeedback onPress={() => {Linking.openURL('bitcoin:35W3nrEVSq7BdrnXc4ZPGPVMFFhDhmxLR2')}}>
              <Image style={{width: 34, height: 34}} source={require('./assets/img/bitcoin.png')}/>
            </TouchableWithoutFeedback>
            <Text style={{paddingLeft: 8}}>35W3nrEVSq7BdrnXc4ZPGPVMFFhDhmxLR2</Text>
          </View>
          <View style={{flexDirection: 'row', justifyContent: 'space', alignItems: 'center', paddingBottom: 8}}>
            <TouchableWithoutFeedback onPress={() => {Linking.openURL('litecoin:MWYuAdzNKRDtocskRmqFwpMD9Fg9U7Yr5R')}}>
              <Image style={{width: 34, height: 34}} source={require('./assets/img/litecoin.png')}/>
            </TouchableWithoutFeedback>
            <Text style={{paddingLeft: 8}}>MWYuAdzNKRDtocskRmqFwpMD9Fg9U7Yr5R</Text>
          </View>
        </View>
        <SoundDivider/>
        <Text style={{paddingBottom: 8}}>{'Version 1.0.0\nHearthstone Patch 18.0.0.54613'}</Text>
        <SoundDivider/>
        <Text style={{paddingBottom: 8, fontSize: 10}}>{'HearthSFX is not affiliated with or endorsed by Blizzard Entertainment. Images, sounds, and descriptions are trademarked and subject to copyright below.'}</Text>
        <Text style={{paddingBottom: 8, fontSize: 10}}>{'Hearthstone®: Heroes of Warcraft™\n©2014 Blizzard Entertainment, Inc. All rights reserved. Heroes of Warcraft is a trademark, and Hearthstone is a registered trademark of Blizzard Entertainment, Inc. in the U.S. and/or other countries.'}</Text>

        <TouchableOpacity style={styles.button} onPress= {() => setInfoModalVisibility('hidden')}>
          <Text style={styles.text}>Close</Text>
        </TouchableOpacity>
      </View>
    )
  }

  const FilterMenu = () => {
    return (
      <View style={{position: 'absolute', right:48, top:56, zIndex: 1, backgroundColor: 'white', visibility:filterMenuVisibility,
      shadowColor: "#000",
      shadowOffset: {
        width: 0,
        height: 2
      },
      shadowOpacity: 0.25,
      shadowRadius: 3.84,
      elevation: 5}} >
        <View style={{padding: 16}}>
          <View style={{flexDirection: 'row', justifyContent: 'space-between'}}>
            <Text style={{paddingRight: 16}}>Minions</Text>
            <Switch onValueChange={changeMinionFilter} value={includeMinions} />
          </View>
          <View style={{flexDirection: 'row', justifyContent: 'space-between', paddingTop: 16}}>
            <Text style={{paddingRight: 16}}>Heroes</Text>
            <Switch onValueChange={changeHeroFilter} value={includeHeroes} />
          </View>
          <View style={{flexDirection: 'row', justifyContent: 'space-between', paddingTop: 16}}>
            <Text style={{paddingRight: 16}}>Spells</Text>
            <Switch onValueChange={changeSpellFilter} value={includeSpells} />
          </View>
          <View style={{flexDirection: 'row', justifyContent: 'space-between', paddingTop: 16}}>
            <Text style={{paddingRight: 16}}>Weapons</Text>
            <Switch onValueChange={changeWeaponFilter} value={includeWeapons} />
          </View>
          <View style={{flexDirection: 'row', justifyContent: 'space-between', paddingTop: 16}}>
            <Text style={{paddingRight: 16}}>Non-Collectible</Text>
            <Switch onValueChange={changeNonCollectibleFilter} value={includeNonCollectible} />
          </View>
        </View>
      </View>
    )
  }

  const styles = StyleSheet.create({
    button: {
      alignItems: "center", padding: 10, justifyContent: 'center', backgroundColor: "#2196F3",
      shadowColor: "#000",
      shadowOffset: {
        width: 0,
        height: 2
      },
      shadowOpacity: 0.25,
      shadowRadius: 3.84,
      elevation: 5
    },

    text: {
      alignItems: "center", justifyContent: 'center', color: "white", fontSize: 14,
    }
  })

  const changeMinionFilter = (newMinionFilter) => {
    setIncludeMinions(newMinionFilter)
    setMyCardData(myData.filter(x =>
          (
            x.type == (newMinionFilter ? 'MINION' : '') ||
            x.type == (includeHeroes ? 'HERO' : '') ||
            x.type == (includeSpells ? 'SPELL' : '') ||
            x.type == (includeWeapons ? 'WEAPON' : '')
          )
            && (x.set === cardSet || cardSet === null)
            && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
          ).slice(0,loadLimit))
  }

  const changeHeroFilter = (newHeroFilter) => {
    setIncludeHeroes(newHeroFilter)
    setMyCardData(myData.filter(x =>
        (
            x.type == (includeMinions ? 'MINION' : '') ||
            x.type == (newHeroFilter ? 'HERO' : '') ||
            x.type == (includeSpells ? 'SPELL' : '') ||
            x.type == (includeWeapons ? 'WEAPON' : '')
          )
            && (x.set === cardSet || cardSet === null)
            && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
          ).slice(0,loadLimit))
  }

  const changeSpellFilter = (newSpellFilter) => {
    setIncludeSpells(newSpellFilter)
    setMyCardData(myData.filter(x =>
      (
            x.type == (includeMinions ? 'MINION' : '') ||
            x.type == (includeHeroes ? 'HERO' : '') ||
            x.type == (newSpellFilter ? 'SPELL' : '') ||
            x.type == (includeWeapons ? 'WEAPON' : '')
          )
            && (x.set === cardSet || cardSet === null)
            && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
          ).slice(0,loadLimit))
  }

  const changeWeaponFilter = (newWeaponFilter) => {
    setIncludeWeapons(newWeaponFilter)
    setMyCardData(myData.filter(x =>
      (
            x.type == (includeMinions ? 'MINION' : '') ||
            x.type == (includeHeroes ? 'HERO' : '') ||
            x.type == (includeSpells ? 'SPELL' : '') ||
            x.type == (newWeaponFilter ? 'WEAPON' : '')
          )
            && (x.set === cardSet || cardSet === null)
            && (x.collectible == true || global.tokenIDs.includes(includeNonCollectible ? x.id : 'N/A'))
          ).slice(0,loadLimit))
  }

  function changeNonCollectibleFilter(newNonCollectibleFilter) {
    setIncludeNonCollectible(newNonCollectibleFilter)
    setMyCardData(myData.filter(x =>
        (
            x.type == (includeMinions ? 'MINION' : '') ||
            x.type == (includeHeroes ? 'HERO' : '') ||
            x.type == (includeSpells ? 'SPELL' : '') ||
            x.type == (includeWeapons ? 'WEAPON' : '')
          )
            && (x.set === cardSet || cardSet === null)
            && (x.collectible == true || global.tokenIDs.includes(newNonCollectibleFilter ? x.id : 'N/A'))
          ).slice(0,loadLimit))
  }

  const ToolbarTitle = () => {
    if(!fontsLoaded) {
      return (<></>)
    }
    else {
      return (<Text style={{fontFamily: Roboto_700Bold, fontSize: 20, color: 'white', fontWeight: '500'}}>HearthSFX</Text>)
    }
  }

  return (

      <>
      <Toolbar
          centerElement={<ToolbarTitle/>}
          searchable={{
            autoFocus: true,
            placeholder: 'Search',
            onChangeText: (criteria) => { search(criteria) },
            onSearchClosed: () => {closeSearch()},
            onSearchPressed: () => {
              setFilterMenuVisibility('hidden')
              setInfoModalVisibility('hidden')
            }
          }}
          rightElement= {
            <>
              <TouchableOpacity style={{paddingTop: 8, paddingRight: 8}} onPress={() => {
                infoModalVisibility == 'hidden' ? setInfoModalVisibility('visible') : setInfoModalVisibility('hidden')
              }}>
                <MaterialCommunityIcons name="information-outline" size={30} color="white" />
              </TouchableOpacity>
              {typeFilter == 'MINION' ?
                <TouchableOpacity style={{paddingTop: 8}} onPress={() => {
                    filterMenuVisibility == 'hidden' ? setFilterMenuVisibility('visible') : setFilterMenuVisibility('hidden')
                  }}>
                <MaterialCommunityIcons name="filter-variant" size={30} color="white" />
              </TouchableOpacity>
            : <></>}

            </>
          }
        />
        <FilterMenu/>
        <InfoModal/>
          <View style={{ flex: 1, padding: 8, flexDirection: 'row'}}>
            <FlatList
              data={myCardData}
              renderItem={({ item }) => (
                <TouchableOpacity onPress= {() => {
                              setCard(item)
                              setCards([item])
                              if (cardDisplayed == 'none') {
                                setCardDisplayed('')
                              }
                              setFilterMenuVisibility('hidden')
                              setInfoModalVisibility('hidden')
                            }}>

                  <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
                  , justifyContent: 'space-between'
                  , marginTop: 4, marginBottom: 4, marginRight: 8}}>
                    <SmallClassImage cardClass={item.cardClass} multiClassGroup={item.multiClassGroup}/>
                    <Text style={{flexGrow: 1}} >{item.name}</Text>
                    {item.type == 'MINION' && !['SCH_239','SCH_271t','SCH_357t','SCH_605','SCH_612t','SCH_616','SCH_711','SCH_714'].includes(item.id) ?
                      <>
                      {(['KAR_114','DAL_058','LOOT_013','AT_114'].includes(item.id)) ? <></> :
                        <>
                          <SoundButton title='Play' cardID={item.id} sound='P' />
                          <View style={{padding: 4}} />
                        </>
                      }
                      {(['DAL_058','LOOT_013'].includes(item.id)) ? <></> :
                        <>
                          <SoundButton title='Attack' cardID={item.id} sound='A' />
                          <View style={{padding: 4}} />
                        </>
                      }
                      <SoundButton title='Death' cardID={item.id} sound='D' />
                      </>
                      : <></>
                  }
                  </View>
                  </TouchableOpacity>

              )}
              ItemSeparatorComponent={FlatListItemSeparator}
              ListFooterComponent={CardListFooter}
              ref={flatListRef}

            />
            <View style={{justifyContent:'center', display:cardDisplayed}}>
               <Card card={card}
                  openNewCard={openNewCard}
                  closeCards={closeCards}
                  returnToPreviousCard={returnToPreviousCard}
                  previousCardExists={(cards.length>1) ? true : false}/>
            </View>
          </View>
          </>
        )
      }


export default CardListTab;
